img.langBtn {
    transition: 1s;
    width: 48px;
}

img.langBtn:hover {
    scale: 1.2;
}

a.webBtn {
    transition: 100ms;
    color: rgba(255, 255, 255, 0.87);
}

a.webBtn:hover {
    color: rgba(114, 114, 114, 0.87);
    font-weight: bold;
}

.customText {
    font-size: 1.5rem;
    color: rgb(180, 180, 180);
    font-family: 'FrankRuhlLibre';
    transition: 500ms;
}

.titleText {
    color: #0d0d0d;
}

@media only screen and (max-width: 992px) {
    .customText {
        font-size: 1.3rem;
    }
}

@media only screen and (max-width: 768px) {
    .customText {
        font-size: 1.1rem;
    }
}

@media only screen and (max-width: 576px) {
    .customText {
        font-size: 0.9rem;
    }
}